<template>
    <div ref="elementRef" :style="Style" class="danmu absolute text-white z-40">{{ data }}</div>

</template>

<style>
.danmu {
    white-space: nowrap; /* 确保内容不换行 */
    animation: moveLeft 15s linear forwards; /* 使用动画让内容向右移动 */
    clip: rect(auto, auto, auto, 100%);
    }
    
    @keyframes moveLeft {
        0% {
            transform: translateX(50vw);
        }
        100% {
            transform: translateX(-100vw);
        }
    }
</style>

<script setup>
import { defineProps,ref } from 'vue';

// eslint-disable-next-line no-unused-vars
const props = defineProps({
    data: String
});
const elementRef = ref(null);

setTimeout(function(){
    const element = elementRef.value;
    element.parentNode.removeChild(element);
},90000)

const randomPercentage = Math.floor(Math.random() * 90); // 生成0到100之间的随机整数
const topValue = randomPercentage + '%'; // 转换为百分比值
const Style = ref(`top: ${topValue};`)
</script>
