const button = ref('bi bi-play-circle');
const range_lan = ref('0px');
const class_range = ref('home-range hide');

// 暂停
function pauseVid() {
	audioRef.value.pause();
	class_range.value = 'home-range hide'
	button.value = 'bi bi-play-circle'
}

//初始化音量


//拖动进度条
function timec() {
	audioRef.value.currentTime = pro.value.value;
}
//同步进度条
const pro1 = function pro1() {
	pro.value.max = audioRef.value.duration;
	range_lan.value = audioRef.value.currentTime * 12.5 / audioRef.value.duration + 0.125 + 'rem'
	document.title = 'Home & 正在播放» ' + MusicNameList[Page.value] + ' | 白澪'

}
//控制音量
function volume() {
	audioRef.value.volume = range.value.value / 100;
}
//开始
function playVid() {
	if (audioRef.value.paused) {
		class_range.value = 'home-range'
		button.value = 'bi bi-pause-circle'
		audioRef.value.play();
		pro.value.max = audioRef.value.duration;
		setInterval(pro1, 0.1);
		volume()
	}
	else {

		pauseVid()
	}

}

