import { h, createApp } from "vue";
import Message from "./msgError.vue"; // 将你的通知组件保存为单文件组件

export function sendError(targetId,type, data) {
    const container = document.createElement("div");

    document.getElementById(targetId).appendChild(container);

    const app = createApp({
        render: () => h(Message, { data: data,type:type })
    });

    app.mount(container);


}