import { createApp } from 'vue'
import '@/assets/main.scss'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from '@/assets/locales/i18n'
import { sendMsg } from '@/components/sendMsg'
import { sendError } from '@/components/sendError'
import { sendDanmu } from '@/components/LiveCom/sendDanmu'
import { gSendMsg } from '@/components/gSendMsg'

const app = createApp(App)
app.config.globalProperties.$sendMsg = sendMsg;
app.config.globalProperties.$sendMsg = sendError;
app.config.globalProperties.$sendMsg = sendDanmu;
app.config.globalProperties.$sendMsg = gSendMsg;

app.use(i18n)
app.use(store)
app.use(router)
app.mount('#app')


